// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'

function toggleHidden(els: HTMLElement[]) {
  for (const el of els) {
    el.hidden = !el.hidden
  }
}

on('combobox-commit', '.js-add-access-search', function (event) {
  if (!(event.target instanceof Element)) return
  const container = event.target.closest<HTMLElement>('.js-add-access-search')!
  const item = event.target.closest<HTMLElement>('.js-autocomplete-item')!

  const selectionBadge = container.querySelector<HTMLElement>('.js-selected-assignee-badge')!
  const itemSelectionBadge = item.querySelector<HTMLElement>('.js-selection-badge')!
  selectionBadge.innerHTML = itemSelectionBadge.innerHTML

  toggleHidden(
    Array.from(container.querySelectorAll('.js-add-assignment-search-selected, .js-add-assignment-search-selection')),
  )
})

on('click', '.js-cancel-selection', function (event) {
  if (!(event.target instanceof Element)) return
  const form = event.target.closest<HTMLElement>('.js-add-assignment-form')!

  const input = form.querySelector<HTMLInputElement>('#js-org-role-add-assignment-search-input')!
  input.value = ''

  toggleHidden(
    Array.from(form.querySelectorAll('.js-add-assignment-search-selected, .js-add-assignment-search-selection')),
  )
  // because this does not trigger the change event of the form, I trigger it here
  const formClearEvent = new Event('change')
  form.dispatchEvent(formClearEvent)
})

// Observe the save button
observe('.js-add-assignment-form', form => {
  const saveButton = form.querySelector<HTMLButtonElement>('#js-add-assignment-button')
  if (!form || !saveButton) {
    return
  }

  // need to manually set the radio buttons to required to use the form validation
  const roles = form.querySelectorAll<HTMLInputElement>('#org-role-role-selection, input')
  for (const roleRadio of roles) {
    roleRadio.required = true
  }

  // handling enabling the button
  form.addEventListener('change', () => {
    if (!(form as HTMLFormElement).checkValidity()) {
      saveButton.setAttribute('aria-disabled', 'true')
      saveButton.disabled = true
    } else {
      saveButton.removeAttribute('aria-disabled')
      saveButton.disabled = false
    }
  })
})
