import {hasMatch} from 'fzy.js'
import {
  type SearchProvider,
  type FilterProvider,
  FilterItem,
  type QueryEvent,
  type QueryFilterElement,
  SearchItem,
  Octicon,
} from '@github-ui/query-builder-element/query-builder-api'
import type {QueryBuilderElement} from '@github-ui/query-builder-element'

interface Suggestion {
  value: string
}

const getSearchUrl =
  document.querySelector<HTMLFormElement>('#org-role-assignment-search-combobox-form')?.getAttribute('action') || ''

const filterSuggestions = (suggestions: string[], query: string) =>
  suggestions.filter(suggestion => {
    if ((query && hasMatch(query, suggestion)) || query === '') return suggestion
  })

function getSearchInput(): HTMLElement {
  return document.querySelector<HTMLElement>('query-builder#query-builder-org-role-assignment-search-combobox')!
}

async function fetchRoleSuggestions(): Promise<Suggestion[]> {
  return JSON.parse(getSearchInput().getAttribute('data-suggestable-roles')!)
}

const roles = async (): Promise<Suggestion[]> => await fetchRoleSuggestions()

class SearchItemProvider extends EventTarget implements SearchProvider {
  priority = 1
  name = 'Search'
  singularItemName = 'search item'
  value = 'search'
  type = 'search' as const

  constructor(public queryBuilder: QueryBuilderElement) {
    super()

    this.queryBuilder.addEventListener('query', this)
    this.queryBuilder.attachProvider(this)
  }

  handleEvent(event: QueryEvent) {
    if (String(event) === '') return
    this.dispatchEvent(
      new SearchItem({
        priority: 1,
        value: event.toString(),
        icon: Octicon.Search,
        scope: 'GENERAL',
        action: {
          url: `${getSearchUrl}?query=${event.toString()}`,
        },
      }),
    )
  }
}

class RoleProvider extends EventTarget implements FilterProvider {
  name = 'Roles'
  singularItemName = 'role'
  value = 'role'
  priority = 2
  type = 'filter' as const

  constructor(public queryBuilder: QueryBuilderElement) {
    super()

    this.queryBuilder.addEventListener('query', this)
    this.queryBuilder.attachProvider(this)
  }

  async handleEvent(event: QueryEvent) {
    const lastQuery = event.parsedQuery.at(-1)
    const lastQueryValue = lastQuery?.value || ''
    const lastQueryType = lastQuery?.type
    const lastQueryFilter = (lastQuery as QueryFilterElement)?.filter || ''

    if (lastQueryType !== 'filter' && (hasMatch(lastQueryValue, this.value) || lastQueryValue === '')) {
      this.dispatchEvent(new Event('show'))
    }

    if (lastQueryType !== 'filter' || lastQueryFilter !== this.value) return

    const getRoleData = await roles()
    const roleValues = getRoleData.map(role => role.value)

    const filteredRoles = filterSuggestions(roleValues, lastQueryValue)

    for (const role of filteredRoles) {
      this.dispatchEvent(new FilterItem({filter: 'role', value: role}))
    }
  }
}

class AssigneeType extends EventTarget implements FilterProvider {
  name = 'Assignee Types'
  singularItemName = 'Assignee Type'
  value = 'is'
  priority = 3
  type = 'filter' as const

  constructor(public queryBuilder: QueryBuilderElement) {
    super()

    this.queryBuilder.addEventListener('query', this)
    this.queryBuilder.attachProvider(this)
  }

  async handleEvent(event: QueryEvent) {
    const lastQuery = event.parsedQuery.at(-1)
    const lastQueryValue = lastQuery?.value || ''
    const lastQueryType = lastQuery?.type
    const lastQueryFilter = (lastQuery as QueryFilterElement)?.filter || ''

    if (lastQueryType !== 'filter' && (hasMatch(lastQueryValue, this.value) || lastQueryValue === '')) {
      this.dispatchEvent(new Event('show'))
    }

    if (lastQueryType !== 'filter' || lastQueryFilter !== this.value) return

    const types = ['user', 'team']
    for (const state of types) {
      this.dispatchEvent(new FilterItem({filter: 'is', value: state}))
    }
  }
}

document.addEventListener('query-builder:request-provider', (event: Event) => {
  new SearchItemProvider(event.target as QueryBuilderElement)
  new RoleProvider(event.target as QueryBuilderElement)
  new AssigneeType(event.target as QueryBuilderElement)
})
